//
// Language data, sorted by number of native speakers
// https://en.wikipedia.org/wiki/List_of_languages_by_number_of_native_speakers
// fields: code, name, english name, right to left?
//

const DATA = {
  zh: ['zh', '', 'Chinese'],
  es: ['es', 'Espaol', 'Spanish'],
  en: ['en', 'English'],
  hi: ['hi', '', 'Hindi'],
  ar: ['ar', '', 'Arabic', true],
  pt: ['pt', 'Portugus', 'Portugues'],
  bn: ['bn', '', 'Bengali'],
  ru: ['ru', 'Pycc', 'Russian'],
  ja: ['ja', '', 'Japanese'],
  pa: ['pa', '', 'Punjabi'],
  de: ['de', 'Deutsch', 'German'],
  ms: ['ms', ' ', 'Malay'],
  te: ['te', '', 'Telugu'],
  vi: ['vi', 'Ting Vit', 'Vietnamese'],
  ko: ['ko', '', 'Korean'],
  fr: ['fr', 'Franais', 'French'],
  mr: ['mr', '', 'Marathi'],
  ta: ['ta', '', 'Tamil'],
  ur: ['ur', '', 'Urdu'],
  fa: ['fa', '', 'Farsi'],
  tr: ['tr', 'Trke', 'Turkish'],
  it: ['it', 'Italiano', 'Italian'],
  th: ['th', '', 'Thai'],
  gu: ['gu', 'Gujarati'],
  pl: ['pl', 'Polski', 'Polish'],
  ml: ['ml', 'Malayalam'],
  uk: ['uk', 'Ukrainian'],
  sw: ['sw', 'Swahili'],
  uz: ['uz', 'Uzbek'],
  ro: ['ro', 'Romanian'],
  nl: ['nl', 'Nederlands', 'Dutch'],
  sr: ['sr', 'Serbian'],
  el: ['el', '', 'Greek'],
  ca: ['ca', 'Catal', 'Catalan'],
  he: ['he', '', 'Hebrew', true],
  sl: ['sl', 'Slovenina', 'Slovenian'],
  lt: ['lt', 'Lietuvi kalba', 'Lithuanian']
}

export default class Language {

  constructor(data_entry) {
    this.code = data_entry[0] || 'xx'
    this.name = data_entry[1]  || '????'
    this.en_name = data_entry[2] || '????'
    this.rtl = data_entry[3]
  }

  static find(code) {
    return new Language(DATA[code])
  }

}



